'use client'

import { useState } from 'react'
import Navbar from '@/components/Navbar'
import { ShieldCheck, Mail, Lock, LogIn, School } from 'lucide-react'
import { useRouter } from 'next/navigation'

export default function LoginPage() {
    const router = useRouter()
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')

    const handleSsoLogin = () => {
        // 模拟学校 SSO 认证跳转逻辑
        alert('正在重定向至学校统一身份认证门户 CAS/OAuth2...')
        setTimeout(() => {
            localStorage.setItem('user_role', 'student')
            localStorage.setItem('is_logged_in', 'true')
            router.push('/')
        }, 1500)
    }

    return (
        <main>
            <Navbar />

            <section className="section container" style={{ paddingTop: '160px', display: 'flex', justifyContent: 'center' }}>
                <div className="glass" style={{ width: '100%', maxWidth: '450px', padding: '3rem' }}>
                    <div style={{ textAlign: 'center', marginBottom: '3rem' }}>
                        <div style={{ display: 'inline-flex', padding: '1rem', background: 'rgba(99, 102, 241, 0.1)', borderRadius: '20px', color: 'var(--primary)', marginBottom: '1.5rem' }}>
                            <ShieldCheck size={48} />
                        </div>
                        <h2 style={{ fontSize: '2rem', marginBottom: '0.5rem' }}>欢迎回来</h2>
                        <p style={{ fontSize: '0.9rem' }}>使用您的学校账号或个人账号登录</p>
                    </div>

                    <div className="grid" style={{ gap: '1.5rem' }}>
                        <div>
                            <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>邮箱/学号</label>
                            <div className="glass" style={{ display: 'flex', alignItems: 'center', padding: '0 1rem', border: '1px solid var(--glass-border)' }}>
                                <Mail size={18} color="#64748b" />
                                <input
                                    type="text"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                    style={{ background: 'transparent', border: 'none', padding: '1rem', color: 'white', width: '100%', outline: 'none' }}
                                    placeholder="name@university.edu.cn"
                                />
                            </div>
                        </div>

                        <div>
                            <label style={{ display: 'block', marginBottom: '0.5rem', fontSize: '0.9rem', color: '#94a3b8' }}>密码</label>
                            <div className="glass" style={{ display: 'flex', alignItems: 'center', padding: '0 1rem', border: '1px solid var(--glass-border)' }}>
                                <Lock size={18} color="#64748b" />
                                <input
                                    type="password"
                                    value={password}
                                    onChange={(e) => setPassword(e.target.value)}
                                    style={{ background: 'transparent', border: 'none', padding: '1rem', color: 'white', width: '100%', outline: 'none' }}
                                    placeholder="••••••••"
                                />
                            </div>
                        </div>

                        <button className="btn btn-primary" style={{ width: '100%', padding: '1rem', justifyContent: 'center' }}>
                            <LogIn size={20} /> 账号登录
                        </button>

                        <div style={{ display: 'flex', alignItems: 'center', gap: '1rem', color: '#475569', margin: '1rem 0' }}>
                            <div style={{ flex: 1, height: '1px', background: 'var(--glass-border)' }}></div>
                            <span style={{ fontSize: '0.8rem' }}>或者</span>
                            <div style={{ flex: 1, height: '1px', background: 'var(--glass-border)' }}></div>
                        </div>

                        <button onClick={handleSsoLogin} className="btn btn-glass" style={{ width: '100%', padding: '1rem', justifyContent: 'center', gap: '0.75rem', borderColor: 'var(--primary)' }}>
                            <School size={20} color="var(--primary)" /> 学校统一身份认证
                        </button>
                    </div>

                    <p style={{ textAlign: 'center', marginTop: '2rem', fontSize: '0.85rem' }}>
                        还没有账号？ <a href="/signup" style={{ color: 'var(--primary)', fontWeight: 'bold' }}>立即注册</a>
                    </p>
                </div>
            </section>
        </main>
    )
}
